export const getStatusDotClass = (status: string) => {
  const map: Record<string, string> = {
    '未开始': 'bg-blue-400',
    '训练完成': 'bg-green-400',
    '训练失败': 'bg-red-400',
    '运行中': 'bg-purple-400'
  };
  return map[status];
};